#loop_station {
  position: fixed;
  max-width: 1600px;
  height: 100%;
}

body {
  overflow: hidden;
}

#global_controls {
  position: relative;
  max-width: 100%;
  height: 180px;
  /*background-color:#1d1e22f0;*/
  background-image: repeating-conic-gradient(
    black 0deg 10deg,
    white 10deg 20deg,
    gray 20deg 30deg
  );
  margin-top: 10px;
  min-width: 1300px;
}

.module {
  margin-top: 180px;
  width: 20%;
  height: 250px;
  float: left;
  background-color: black;
}

#mod_2 {
  background-color: white;
}

#mod_4 {
  background-color: white;
}

.console_control {
  border: 3px solid #383a38;
  color: #5c5f5c;
  background-color: black;
}

#global_sp {
  position: absolute;
  transform: translate(50%, -50%);
  margin-top: 130px;
  margin-left: 35%;
  width: 60px;
  height: 60px;
  font-size: 120%;
  border-radius: 100%;
  text-align: center;
  line-height: 60px;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: Fantasy, Copperplate;
  box-shadow: inset 0 0 6px #ffffff;
}

.sp_button {
  position: absolute;
  margin-top: 1%;
  margin-left: 1%;
  color: #5c5f5c;
  border: 3.5px solid #383a38;
  background-color: black;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  text-align: center;
  line-height: 50px;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 20px;
  font-family: Fantasy, Copperplate;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  box-shadow: inset 0 0 6px #ffffff;
}

.even {
  background-color: white;
  box-shadow: inset 0 0 4px #000000;
}

.menu_scroll {
  position: absolute;
  float: left;
  color: white;
  background-color: black;
  width: 23px;
  height: 23px;
  padding: 2px;
  border-radius: 100%;
  text-align: center;
  line-height: 23px;
  margin-top: 38.5px;
  margin-left: 66.3%;
  font-size: 20px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transform: translate(50%, -50%);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

#previous {
  transform: translate(50%, -50%);
  margin-top: 38.5px;
  margin-left: 63%;
}
#select {
  margin-top: 73px;
  margin-left: 64.6%;
}

#global_clear {
  position: absolute;
  transform: translate(-50%);
  margin-top: 2.5%;
  left: 90%;
  padding: 3px;
  width: 50px;
  height: 20px;
  border-radius: 25%;
  text-align: center;
  font-size: 18px;
  line-height: 20px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: Fantasy, Copperplate;
  box-shadow: inset 0 0 5px #ffffff;
}

.clear_module {
  float: left;
  position: absolute;
  transform: translate(-50%, 50%);
  width: 35px;
  height: 15px;
  text-align: center;
  margin-left: 17%;
  margin-top: 1%;
  padding: 3px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: Fantasy, Copperplate;
  color: #5c5f5c;
  background-color: white;
  border: 3px solid #383a38;
  border-radius: 25%;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: Fantasy, Copperplate;
  font-size: 80%;
  box-shadow: inset 0 0 4px #000000;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

div .odd {
  background-color: black;
  box-shadow: inset 0 0 5px #ffffff;
}

.rewind {
  float: left;
  position: absolute;
  transform: translate(-50%, 50%);
  margin-left: 17%;
  margin-top: 5%;
  background-color: white;
  width: 40px;
  height: 40px;
  border: 3.5px solid #383a38;
  border-radius: 100%;
  text-align: center;
  line-height: 37px;
  vertical-align: middle;
  color: #5c5f5c;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 150%;
  font-family: Fantasy, Copperplate;
  box-shadow: inset 0 0 6px #000000;
}

div .modifica {
  color: #62d12a;
  border: 2.5px solid #62d12a;
}

.rec_button {
  position: relative;
  margin: auto;
  border-radius: 100%;
  width: 80px;
  height: 80px;
  vertical-align: middle;
  text-align: center;
  line-height: 80px;
  font-size: 30px;
  /*bottom:30%;*/
  color: #5c5f5c;
  background-color: black;
  border: 6px solid #5c5f5c;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: Fantasy, Copperplate;
  top: 95px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  box-shadow: inset 0 0 6px #ffffff;
}

#rec2 {
  background-color: white;
  box-shadow: inset 0 0 4px #000000;
}

#rec4 {
  background-color: white;
  box-shadow: inset 0 0 4px #000000;
}

body {
  background-color: #000000c4;
}

.volume {
  position: absolute;
  transform: rotate(270deg);
  bottom: -175px;
  margin-left: -2%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

#control_panel {
  position: absolute;
  width: 25%;
  height: 30%;
  background-color: #666b7af2;
  /*border:2px solid #5a6070f2;*/
  transform: translate(-50%, -50%);
  top: 30%;
  left: 50%;
  /*cursor:none;*/
  cursor: not-allowed;
  resize: none;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  font-size: 96%;
  font-family: Monospace, Monaco;
  text-align: center;
}

